import { Component, OnInit, ViewChild } from '@angular/core';
import { WebDataRocksPivot } from './webdatarocks/webdatarocks.angular4';
import {DynamicReportService} from './dynamic-report.service';
@Component({
  selector: 'app-dynamic-report',
  templateUrl: './dynamic-report.component.html',
  styleUrls: ['./dynamic-report.component.scss']
})
export class DynamicReportComponent implements OnInit {

  constructor(public reportService: DynamicReportService) { }

  @ViewChild('pivot1') child: WebDataRocksPivot;

  onPivotReady(pivot: WebDataRocks.Pivot): void {
    console.log('[ready] WebDataRocksPivot', this.child);
  }
  ngOnInit() {
    this.getOngoingMelt();
  }

  onReportComplete(): void {
    this.child.webDataRocks.off('reportcomplete');
    this.child.webDataRocks.setReport({
      dataSource: {
        filename: 'http://52.26.246.107:3000/api/uploads/customer.json'
      }, slice: {
        rows: [
          {
            uniqueName: 'Date',
            filter: {
              members: ['Date.[Invalid date]'],
              negation: true
            }
          },
          {
            uniqueName: 'customerName'
          }
        ],
        columns: [
          {
            uniqueName: 'partId'
          }, {
            uniqueName: 'batchId'
          }],
        measures: [
          {
            uniqueName: 'meltWeight'
          }, {
            uniqueName: 'quantity'
          }],
      },
    });
  }
  getOngoingMelt() {
    this.reportService.getOngoing().subscribe((data) => {
      console.log(data);
    }, (error) => {
      console.log(error);
    });
  }
}
