import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import {MaterialModule} from '../../../app.module';
import {HttpModule} from '@angular/http';
import { WebDataRocksPivot } from './webdatarocks/webdatarocks.angular4';
import {CUSTOM_ELEMENTS_SCHEMA} from '@angular/core';
import { DynamicReportComponent } from './dynamic-report.component';
import {DynamicReportRoutingModule} from './dynamic-report-routing.module';
import {DynamicReportService} from './dynamic-report.service';

@NgModule({
  declarations: [DynamicReportComponent, WebDataRocksPivot],
  imports: [
    CommonModule,
    MaterialModule,
    HttpModule, DynamicReportRoutingModule
  ],
  providers: [
    DynamicReportService
  ],
  schemas: [
    CUSTOM_ELEMENTS_SCHEMA
  ]
})
export class DynamicReportModule { }
